<template>
    <div class="come-on">
        <div class="bg" />
        <fly :show="lanternState" />

        <h1>众志成城</h1>
        <p>让我们一起为武汉加油！</p>

        <div
            v-if="lanternState && !isApp"
            class="more"
            @click="clickMore">
            查看更多疫情资讯
        </div>

        <div
            v-show="!lanternState"
            class="lantern"
            @click="clickLantern" />

        <div
            v-if="!lanternState"
            class="pray">
            点击孔明灯祈福
        </div>
        <div
            v-else
            :class="[isApp ? 'pray-succ xiangkan': 'pray-succ']">
            你放飞了第 <span>{{ flyNum }}</span> 个孔明灯
        </div>

        <div
            v-if="lanternState"
            class="invite"
            @click="clickInvite">
            邀请好友点亮
        </div>

        <div class="footer">
            想看资讯<i/>祈福武汉
        </div>

        <div
            class="mask"
            v-if="showMask"
            @click="showMask=false" />
    </div>
</template>

<script>
import {
    isXiangkan,
    isIOS,
    isWechat,
    isMiuiBrowser,
} from '@/utils/ua';
import { getApkVersion } from '@/utils/jsApiV2/client';
import { shareWithSocialType } from '@/utils/jsApiV2/act';
import { setShareInfo } from '@/utils/share';
import { wuhanShare } from '@/api/pray';
import config from './data.json';
import Fly from './Fly';

export default {
    name: 'ComeOn',
    components: {
        Fly,
    },
    props: {
        stat: {
            type: Object,
            default: () => ({}),
        },
    },
    data() {
        return {
            lanternState: false,
            flyNum: 28770,
            showMask: false,
            isApp: isXiangkan(),
            xiangkan: 'com.xiangkan.android',
            wechat: 'com.tencent.mm',
            links: {
                feiyan: 'bikan://goto/newsTab?switchChannelCode=抗击肺炎&start_source=qifu',
                redian: 'https://content.xiangkannews.cn/news',
                download: 'http://a.app.qq.com/o/simple.jsp?pkgname=com.xiangkan.android&ckey=CK1451493267427',
                thumb: 'http://t1.market.xiaomi.com/download/Browser/037694cc113bc77f3b25b25b719b51a34774218fd/thumb.jpg',
                base64Img: config.imgData.qifu,
            },
        };
    },
    methods: {
        clickLantern() {
            this.$o2o({
                e_c: '活动放飞页',
                e_a: '点击',
                e_n: '轻触放飞孔明灯',
                e_x: this.stat,
            });

            wuhanShare().then((ret) => {
                this.flyNum = ret;
                this.lanternState = true;

                this.$o2o({
                    e_c: '活动放飞页',
                    e_a: '成功',
                    e_n: '放飞成功',
                    e_x: this.stat,
                });

                this.replaceHistoryState();
            }).catch(() => {
                this.$toast('操作失败，请稍后重试');
            });
        },
        clickMore() {
            this.$o2o({
                e_c: '活动完成页',
                e_a: '点击',
                e_n: '查看更多疫情相关资讯',
                e_x: this.stat,
            });

            if (this.hasXiangkan()) {
                window.location.href = this.links.feiyan;
            } else if (isIOS()) {
                window.location.href = this.links.redian;
            } else {
                window.location.href = this.links.download;
            }
        },
        clickInvite() {
            this.$o2o({
                e_c: '活动完成页',
                e_a: '点击',
                e_n: '邀请好友点亮',
                e_x: this.stat,
            });

            if (this.isApp || isMiuiBrowser()) {
                this.$o2o({
                    e_c: '活动完成页',
                    e_a: '成功',
                    e_n: '分享朋友圈成功',
                    e_x: this.stat,
                });

                if (this.hasWechat()) {
                    shareWithSocialType({
                        title: `我放飞了第${this.flyNum}个孔明灯，众志成城一起为武汉疫情祈福加油！`,
                        url: `${window.location.origin}/mobile-v2/act/pray?social=wechat&people=${this.flyNum}`,
                        thumbUrl: this.isApp ? this.links.thumb : this.links.base64Img,
                        socialType: this.isApp ? 3 : 5,
                    });
                } else {
                    this.$toast('尚未安装微信，请先安装微信');
                }
            } else if (isWechat()) {
                this.showMask = true;
            }
        },
        hasXiangkan() {
            return getApkVersion(this.xiangkan) > 0;
        },
        hasWechat() {
            return getApkVersion(this.wechat) > 0;
        },
        replaceHistoryState() {
            const title = `我放飞了第${this.flyNum}个孔明灯，众志成城一起为武汉疫情祈福加油！`;
            const url = `${window.location.origin}/mobile-v2/act/pray?social=wechat&people=${this.flyNum}`;

            history.replaceState(null, null, url);

            setShareInfo({
                title,
                summary: '众志成城抗击肺炎，中国加油！武汉加油！',
                pic: 'http://t1.market.xiaomi.com/download/Browser/037694cc113bc77f3b25b25b719b51a34774218fd/thumb.jpg',
                url,
            });
        },
    },
};
</script>

