<template>
    <div
        class="slide"
        @touchstart="touchStart"
        @touchend="touchEnd"
    >
        <!-- 视频封面 -->
        <div
            class="slide-cover"
            v-show="showCover"
        >
            <div class="slide-bg"/>
            <div
                class="play-btn"
                @click="play()"
            />
        </div>

        <!-- 视频转场 -->
        <div
            class="slide-change"
            v-for="(item, index) in changeData"
            v-show="changeCurr == index + 1"
            :key="`change${index}`"

        >
            <div :class="`change-bg change-bg_${index + 1}`"/>

            <transition
                name="fade-down"
                @after-enter="changeAfterEnter(index + 1)"
            >
                <div
                    class="change-tips"
                    v-show="changeCurr == index + 1"
                >
                    <span
                        v-for="title in item.title"
                        :key="title"
                    >
                        {{ title }}
                    </span>
                </div>
            </transition>
        </div>

        <!-- 视频幻灯 -->
        <div
            class="slide-item"
            v-for="(item, index1) in slideData"
            v-show="curr == index1 + 1"
            :key="`slide${index1}`"
        >
            <!-- 切换图片 -->
            <transition
                name="fade-in"
                @after-enter="slideAfterEnter(index1 + 1)"
            >
                <div
                    class="bg"
                    v-show="curr == index1 + 1"
                >
                    <div
                        :class="`bg__content bg__content_${index1 + 1}`"
                    />
                </div>
            </transition>

            <!-- 切换文案 -->
            <transition name="fade-up">
                <div
                    class="info"
                    v-show="curr == index1 + 1"
                >
                    <span class="num">
                        {{ slideData[index1].num }}
                    </span>

                    <div class="title">
                        <span
                            v-for="(title, key) in slideData[index1].title"
                            :key="`info${key}`"
                        >
                            {{ title }}
                        </span>
                    </div>

                    <div class="line"/>

                    <div class="desc">
                        <span
                            v-for="(desc, key1) in slideData[index1].desc"
                            :key="`desc${key1}`"
                        >
                            {{ desc }}
                        </span>
                    </div>

                    <p class="time">{{ slideData[index1].time }}</p>
                </div>
            </transition>
        </div>

        <div class="tips">
            <span class="tips__curr">{{ currPage }}</span>
            <span class="tips__total">0{{ this.slideData.length }}</span>
        </div>

        <div
            class="slide-tips"
            v-show="!showCover"
        />

        <span class="footer">想看资讯</span>
    </div>
</template>

<script>
import slideData from './data.json';

export default {
    name: 'Slide',
    data() {
        return {
            curr: 0, // 当前轮播图
            changeCurr: 0, // 当前转场图
            stop: false, // 轮播暂停
            showCover: true,
            slideData: slideData.data,
            changeData: [
                { title: ['肺炎来袭', '武汉告急'] },
                { title: ['一方有难', '八方支援'] },
            ],
            touchY: 0, // 滑动位置
        };
    },
    props: {
        stat: {
            type: Object,
            required: true,
        },
    },
    computed: {
        currPage() {
            return this.curr < 10 ? `0${this.curr}` : this.curr;
        },
    },
    methods: {
        play() { // 视频播放
            this.showCover = false;
            this.changeStart(1); // 第一个转场
            this.$emit('audio-play', 'slide');

            this.$o2o({
                e_c: '活动首页',
                e_a: '点击',
                e_n: '播放按钮点击',
                e_x: this.stat,
            });
        },
        changeStart(index) { // 转场开始
            this.changeCurr = index;
            this.curr = 0;
        },
        changeAfterEnter(index) { // 转场单个动画完成
            const target = this;
            const slideTarget = index === 2 ? 5 : 1;

            if (target.stop) {
                return;
            }

            target.slideLog(`zc${index}`);

            setTimeout(() => {
                target.changeStop();
                target.curr += slideTarget;
            }, 1500);
        },
        changeStop() { // 转场结束
            this.changeCurr = 0;
        },
        slideAfterEnter(index) { // 轮播页单个动画完成
            const target = this;

            if (target.stop) {
                return;
            }

            target.slideLog(index);

            setTimeout(() => {
                if (index === 4) {
                    target.changeStart(2);
                } else if (target.curr < target.slideData.length) {
                    target.curr += 1;
                } else {
                    target.slideStop();
                }
            }, 1500);
        },
        slideStop() { // 轮播页结束
            const target = this;

            // 轮播结束
            target.stop = true;

            setTimeout(() => {
                target.$emit('slide-stop');
            }, 500);
        },
        touchStart(e) {
            const touches = e.touches[0] || e.changedTouches[0];
            this.touchY = touches.clientY;
        },
        touchEnd(e) {
            const touches = e.touches[0] || e.changedTouches[0];

            if (touches.clientY < this.touchY) { // 上滑进入祈福页
                this.slideStop();

                this.$o2o({
                    e_c: '轮播页',
                    e_a: '点击',
                    e_n: '用户上划跳过',
                    e_x: this.stat,
                });
            }
        },
        slideLog(page) {
            const ext = Object.assign({ page }, this.stat);

            this.$o2o({
                e_c: '轮播页',
                e_a: '曝光',
                e_n: '轮播页曝光',
                e_x: ext,
            });
        },
    },
};
</script>

