<template>
    <div
        :class="[isXiangkan ? 'pray_xiangkan' : '', 'pray']"
    >
        <nav-bar
            v-show="isXiangkan"
            ref="nav-bar"
            class="title-bar"
            title="一起放飞孔明灯，为武汉祈福"
        />

        <slide
            @slide-stop="slideStop"
            @audio-play="audioPlay"
            :stat="stat"
            v-show="showSlide"
        />

        <transition name="fade-in">
            <come-on
                v-show="showCome"
                :stat="stat"
            />
        </transition>

        <div
            :class="[audioBtnStatus === 'pause' ? 'audio-btn_pause' : '', 'audio-btn']"
            @click="audioChange()"
        />
    </div>
</template>

<script>
import { getSharedPrefBoolean } from '@/utils/jsApi';
import {
    isXiangkan,
    isIOS,
    isWechat,
    isAndroid,
} from '@/utils/ua';
import { setShareInfo } from '@/utils/share';
import NavBar from '@/components/NavBar';
import Slide from './Slide';
import ComeOn from './ComeOn';

export default {
    name: 'Pray',
    data() {
        return {
            isXiangkan: isXiangkan(),
            showSlide: true,
            showCome: false,
            stat: {
                status: this.getStatStatus(),
                os: this.getStatOs(),
            },
            social: this.$route.query.social,
            people: this.$route.query.people,
            currPlay: '',
            audioBtnStatus: 'pause',
            homeAudio: new Audio('https://ts.market.mi-img.com/download/Browser/Browser/0d86e4af3fb51d95945a501575266562fb0435b87/home.mp3'),
            slideAudio: new Audio('https://ts.market.mi-img.com/download/Browser/06e3957b180e3934f126dc4938d6a3ec93843e85a/slide.mp3'),
        };
    },
    components: {
        NavBar,
        Slide,
        ComeOn,
    },
    methods: {
        slideStop() {
            this.showSlide = false;
            this.showCome = true;

            this.$o2o({
                e_c: '活动放飞页',
                e_a: '曝光',
                e_n: '孔明灯放飞页面曝光',
                e_x: this.stat,
            });

            // 音频切换播放
            setTimeout(() => {
                this.audioPlay('home');
            }, 500);
        },
        getStatStatus() {
            if (isXiangkan()) {
                return 'app';
            } else if (isWechat()) {
                return 'wechat';
            }

            return 'others';
        },
        getStatOs() {
            if (isIOS()) {
                return 'ios';
            } else if (isAndroid()) {
                return 'android';
            }

            return 'others';
        },
        audioPlay(audioName) { // 音频播放
            this.currPlay = audioName;

            // 获取客户端的音效开关配置
            const voiceSwitch = getSharedPrefBoolean('news_settings_pref', 'receive_coin_voice_switch', true);

            // 端内音频条件限制
            if (!voiceSwitch && this.isXiangkan) {
                return;
            }

            // 播放后, 处理音乐暂停时, 后续场景不再播放
            if (this.currPlay !== '' && this.audioBtnStatus === 'pause') {
                return;
            }

            if (audioName === 'home') {
                this.slideAudio.pause();
                this.homeAudio.play();
                this.homeAudio.loop = true;
            } else {
                this.homeAudio.pause();
                this.slideAudio.play();
                this.slideAudio.loop = true;
            }
        },
        audioChange() { // 点击按钮进行音频切换
            if (this.audioBtnStatus === 'play') {
                this.slideAudio.pause();
                this.homeAudio.pause();
                this.audioBtnStatus = 'pause';

                this.$o2o({
                    e_c: '背景音乐',
                    e_a: '点击',
                    e_n: '背景音乐点击关闭',
                    e_x: this.stat,
                });
            } else {
                this.$o2o({
                    e_c: '背景音乐',
                    e_a: '点击',
                    e_n: '背景音乐点击打开',
                    e_x: this.stat,
                });

                if (this.currPlay !== 'slide') {
                    this.audioBtnStatus = 'play';
                    this.audioPlay('home');
                } else {
                    this.audioBtnStatus = 'play';
                    this.audioPlay('slide');
                }
            }
        },
        listenVisiblityChange() {
            if (document.visibilityState === 'visible' && this.audioBtnStatus === 'play') {
                this.audioPlay(this.currPlay);
            } else {
                this.homeAudio.pause();
                this.slideAudio.pause();
            }
        },
        setWxShareInfo() {
            let title = '一起放飞孔明灯，为武汉祈福';

            if (this.people) {
                title = `我放飞了第${this.people}个孔明灯，众志成城一起为武汉疫情祈福加油！`;
            }

            setShareInfo({
                title,
                summary: '众志成城抗击肺炎，中国加油！武汉加油！',
                pic: 'http://t1.market.xiaomi.com/download/Browser/037694cc113bc77f3b25b25b719b51a34774218fd/thumb.jpg',
                url: window.location.href,
            });
        },
    },
    created() {
        this.$root.hidePlaceholder();
        document.title = '一起放飞孔明灯，为武汉祈福';

        this.$o2o({
            e_c: '活动首页',
            e_a: '曝光',
            e_n: '首页曝光',
            e_x: this.stat,
        });

        if (this.social) {
            this.$o2o({
                e_c: '朋友圈二次打开',
                e_a: '曝光',
                e_n: '朋友圈二次打开',
                e_x: this.stat,
            });
        }

        this.setWxShareInfo();
    },
    mounted() {
        // 主页音频播放
        this.audioBtnStatus = 'play';
        this.audioPlay('home');

        document.addEventListener('visibilitychange', this.listenVisiblityChange);
    },
    destroyed() {
        document.removeEventListener('visibilitychange', this.listenVisiblityChange);
    },
};
</script>

<style scoped>
    .pray {
        line-height: 0;
        background: #000;
    }

    .audio-btn {
        position: fixed;
        top: 20px;
        right: 20px;
        width: 28px;
        height: 28px;
        background: url(./images/audio-btn.png) no-repeat;
        background-size: 28px 28px;
        z-index: 2;
    }

    .audio-btn_pause {
        background: url(./images/audio-btn-pause.png) no-repeat;
        background-size: 28px 28px;
    }

    .pray_xiangkan .audio-btn {
        top: 65px;
    }

    .fade-in-enter-active {
        animation: fadeIn 1.5s ease-in-out;
    }

    @keyframes fadeIn {
        from {
            opacity: 0.1;
        }

        60% {
            opacity: 0.4;
        }

        to {
            opacity: 1;
        }
    }
</style>
