function tryCall(funName, ...args) {
    try {
        return window.miui[funName](...args);
    } catch (e) {
        // console.log('调用app方法', funName, ...args, e);
        return false;
    }
}

function tryCallJSONParse(funName, ...args) {
    try {
        return JSON.parse(window.miui[funName](...args) || '{}');
    } catch (e) {
        // console.log('调用app方法', funName, ...args);
        return false;
    }
}

