export function startDownloadApp({
    packageName,
    appClientId,
    appSignature,
    nonce,
    appRef,
    appChannel,
    ex,
}, jsCallback) {
    try {
        return window.miui.miuiStartDownloadApp(
            packageName, appClientId, appSignature, nonce, appRef, appChannel, ex, jsCallback,
        );
    } catch (e) {
        console.log(
            '调用app方法', 'miuiStartDownloadApp', packageName, appClientId, appSignature, nonce, appRef, appChannel, ex, jsCallback,
        );

        return window.miui.miuiStartDownloadApp(
            packageName, appClientId, appSignature, nonce, appRef, appChannel, jsCallback,
        );
    }
}

